*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: #d2d2d2;
}

.card{
    margin: 6%;
    transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
    background-color: #fff;
    width: 21.25%;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px black;
    float:left;
}

.cards{
    width: 100%;
    height: auto;
}

.card_img{
    width: 100%;
    height: 235px;
    border-top-left-radius:12px ;
    border-top-right-radius:12px ;
}

.card_info{
    background-color: #fff;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px ;
    padding: 16px 24px 24px 24px;
    background-color: #fff;
}

.card_category{
    font-family: sans-serif ;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    color: #868686;
    background-color: #fff;
}

.card_title{
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: sans-serif;
    background-color: #fff;
    text-transform: capitalize;
}

.headding{
    padding: 30px 0;
    text-align: center;
    font-family: sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    font-size: 1.5rem;
    margin-bottom: 30px;
}

.card:hover{
    box-shadow: 0px 30px 18px -8px black;
    transform: scale(1.05,1.05);
}

.button{
    padding: 5px 8px;
    font-family: sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    background-color: #fff;
    outline: none;
    border: 1px solid black;
    cursor: pointer;
}

